.App {
  padding: 30px;
  display: flex;
}

.App > div {
  margin-right: 50px;
}

.isDone {
  color: red;
  font-weight: bold;
  text-decoration: line-through;
}

.activeButton {
  color: lime;
  background-color: dimgrey;
  border-radius: 2px;
}
.error {
  border: red 1px solid;
}

.error-message {
  color: red;
}